const list = {
  provinceList: ['河南', '北京', '河南', '北京', '河南', '北京', '河南', '北京', '河南', '北京', '河南', '北京', '河南', '北京', '河南', '北京', '河南', '北京', '河南', '北京', '河南', '北京', '河南', '北京', '河南', '北京'],
  yearList: ['2010', '2014', '2010', '2014', '2010', '2014', '2010', '2014', '2010', '2014', '2010', '2014', '2010', '2014', '2010', '2014', '2010', '2014', '2010', '2014', '2010', '2014', '2010', '2014'],
};
export default {
  data() {
    return {
      province: '',
      year: '',
      select: '',  // wenke  like
      userName: '',

      bigFrameType: '',
      list: [],
      isShowBigFrame: false,

      error: ''
    };
  },
  methods: {
    clickSelected(type) {
      if (type === this.select) {
        this.select = '';
        return;
      }
      this.select = type;
    },
    showError(msg) {
      this.error = msg;
      setTimeout(() => {
        this.error = '';
      }, 2000)
    },
    onclickButton() {
      if (!this.province) {
        this.showError('请选择高考所在地');
        return;
      }
      if (!this.year) {
        this.showError('请选择高考年份');
        return;
      }
      if (!this.select) {
        this.showError('请选择文理科');
        return;
      }
      if (!this.userName) {
        this.showError('请输入你的名字');
        return;
      }
      // const imgName = `${this.province}_${this.year}_${this.select}`
      this.$router.push({
        path: 'topic',
        query: {
          province: this.province,
          year: this.year,
          select: this.select,
          userName: this.userName,
        }
      });
    },
    onclickFrameSelect(type) {
      this.list = list[type + 'List'];
      this.bigFrameType = type;
      this.isShowBigFrame = true;
    },
    onclickCancelSelect() {
      this.list = [];
      this.isShowBigFrame = false;
    },
    onclickItem(item) {
      this[this.bigFrameType] = item;
      this.isShowBigFrame = false;
      this.list = [];
      this.bigFrameType = '';
    }
  },
  mounted() {
  }
};



